/* src/styles/Header.css */
.header {
    background-color: #333;
    padding: 10px 20px;
    position: sticky;
    top: 0;
    z-index: 1000;
}

    .header ul {
        display: flex;
        justify-content: center;
        list-style: none;
        margin: 0;
        padding: 0;
    }

    .header li {
        margin: 0 15px;
    }

    .header a {
        font-weight: bold;
    }

@media (max-width: 768px) {
    .header ul {
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
        gap: 10px;
    }

    .header li {
        margin: 5px 10px;
    }

    .header a {
        font-size: 0.9em;
        padding: 5px 10px;
    }
}
